.header {
    position: fixed;
    top: 0px;
    width: 100vw;
    min-width: 1200px;
    height: 80px;
    background-color: rgba(256, 256, 256, 1);
    color: #000;
    border-bottom: 1px solid rgba(0, 0, 0, .1);
    box-shadow: 0px 0px 30px rgba(0, 0, 0, .3);
}

