.headerDiv {
    display: flex;
    margin: 0 auto;
    width: 1200px;
    height: 100%;
    justify-content: space-between;
    align-items: center;
}

.logoDiv {
    display: flex;
    width: 20%;
    height: 100%;
    // justify-content: space-between;
    align-items: center;
    line-height: 100%;
}

.logoImg {
    /* width: ; */
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #afafaf;
    margin-right: 20px;
}

.logoInfo {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80%;
    text-align: center;
    font-size: 20px;
}

.navigationBar {
    display: flex;
    width: 45%;
    height: 100%;
    justify-content: space-between;
    align-items: center;
}

#navigationDivs {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    align-items: center;
}

.navigation {
    margin: 10px;
    transition: all .2s;
}

.navigation:hover {
    cursor: pointer;
    color: rgb(73, 177, 245);
}

#search {
    width: 50px;
    height: 50px;
    text-align: center;
    transition: color .2s;
    line-height: 50px;
    font-size: 1em;
}

#search:hover {
    cursor: pointer;
    color: rgb(73, 177, 245);
}
@primary-color: #1DA57A;